import React from "react";
import PropTypes from "prop-types";

import {
  ButtonGroup,
  Button,
  Div,
  Panel,
  PanelHeader,
  PanelHeaderBack,
} from "@vkontakte/vkui";

import "../index.css";

const Faq = ({ id, go }) => {
  return (
    <Panel id={id}>
      <PanelHeader before={<PanelHeaderBack onClick={go} data-to="home" />}>
         
      </PanelHeader>
      <Div className="menu-container">
        <div className="msg center text">
          <div>
            <b>  </b>
          </div>
          <div>
                     
            
          </div>
          <div> : support @ihelpdesk.me</div>
        </div>
        <ButtonGroup className="button-group" mode="vertical" gap="m" stretched>
          <a href="https://www.peterburgregiongaz.ru/faq/" target="_blank">
            <Button size="l" appearance="accent" stretched>
               
            </Button>
          </a>
          <a href="https://vk.com/id818111005" target="_blank">
            <Button size="l" appearance="accent" stretched>
                
            </Button>
          </a>
          <Button
            size="l"
            appearance="accent"
            stretched
            onClick={go}
            data-to="menu"
          >
            
          </Button>
        </ButtonGroup>
      </Div>
    </Panel>
  );
};

Faq.propTypes = {
  id: PropTypes.string.isRequired,
  go: PropTypes.func.isRequired,
};

export default Faq;
