import React from "react";
import PropTypes from "prop-types";
import { parseISO, format } from "date-fns";

import {
  Panel,
  PanelHeader,
  PanelHeaderBack,
  Div,
  Button,
} from "@vkontakte/vkui";

import "../index.css";

const MetersFinish = ({ id, go, accountData }) => {
  // const inputDate = accountData.datepok_pug; //  
  const inputDate = accountData.date; //     
  const parsedDate = parseISO(inputDate);
  const formattedDate = format(parsedDate, "dd.MM.yyyy");

  return (
    <Panel id={id}>
      <PanelHeader before={<PanelHeaderBack onClick={go} data-to="menu" />}>
         
      </PanelHeader>
      <Div className="menu-container">
        <Div className="msg center text">
          <div>    !</div>
          <div>
             :
            <div>
                : <b>{accountData.LS}</b>
            </div>
            <div>
               :{" "}
              <b>
                {formattedDate}: {accountData.pokaz_pug} 3
              </b>
            </div>
          </div>
        </Div>
        <div>
          <Button
            size="l"
            appearance="accent"
            stretched
            onClick={go}
            data-to="home"
          >
            
          </Button>
        </div>
      </Div>
    </Panel>
  );
};

MetersFinish.propTypes = {
  id: PropTypes.string.isRequired,
  go: PropTypes.func.isRequired,
};
export default MetersFinish;
