import React from "react";
import PropTypes from "prop-types";

import { Panel, PanelHeader, Button, Div, Text } from "@vkontakte/vkui";

import logo from "../img/logo.png";
import "./index.css";

const Home = ({ id, go }) => {
  return (
    <Panel id={id}>
      <PanelHeader className="header"> </PanelHeader>
      <div className="menu-container">
        <Div className="home-text">
          <img className="logo" src={logo} />
          <div className="text">
            <Text>
                      
               -
            </Text>
            <Text>
              <b>   :</b>
            </Text>
            <Text>
              <div>   :</div>
              <div>
                <b>      </b>
              </div>
            </Text>
            <Text>
              <div>   : </div>
              <div>
                <b>  </b>
              </div>
            </Text>
            <Text>
              <div>     :</div>
              <div>
                <b> 80.0 </b>
              </div>
            </Text>
            <Text>
              <div>   :</div>
              <div>
                <b> 01.12.2022</b>
              </div>
            </Text>
          </div>
        </Div>
        <Div>
          <Button
            stretched
            appearance="accent"
            size="l"
            mode="primary"
            onClick={go}
            data-to="menu"
          >
              
          </Button>
        </Div>
      </div>
    </Panel>
  );
};

Home.propTypes = {
  id: PropTypes.string.isRequired,
  go: PropTypes.func.isRequired,
};

export default Home;
